.profile__name{
  grid-area: name;
  font-family: 'Inter', ‘Arial’, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 56px;
  margin:0;
  padding:0;
  text-overflow: ellipsis; 
  white-space: nowrap; 
  overflow: hidden;
  max-width: 298px;
}

@media screen and (max-width: 882px) {
  .profile__name {
    font-size: 27px;
    max-width: 201px
  }
}